import React from 'react'

import { GoogleMap, useJsApiLoader, Marker } from '@react-google-maps/api';
const containerStyle = {
  width: '500px',
  height: '500px'
};



const Maps = ({ latitude, longitude }) => {
  const { isLoaded } = useJsApiLoader({
    id: 'google-map-script',
    googleMapsApiKey: "AIzaSyCZaKUc5TnNrlQOUICqnINSBH3BNvUuwOE"
  })

  const center = { lat: latitude, lng: longitude };

  const [map, setMap] = React.useState(null)

  const onLoad = React.useCallback(function callback(map) {
    // This is just an example of getting and using the map instance!!! don't just blindly copy!
    const bounds = new window.google.maps.LatLngBounds(center);
    map.fitBounds(bounds);

    setMap(map)
  }, [])

  const onUnmount = React.useCallback(function callback(map) {
    setMap(null)
  }, [])


  return isLoaded ? (
    <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh',background:'saintgray'}}>
      <div className='bg-danger'>
        <GoogleMap
          mapContainerStyle={containerStyle}
          center={center}
          zoom={8}
          onLoad={onLoad}
          mapTypeId='satellite'
          onUnmount={onUnmount}
        >
          {<Marker position={center} icon={{url: "https://maps.google.com/mapfiles/ms/icons/red-dot.png" }}/>}
        </GoogleMap>
        
      </div>
    </div>
  ) : <></>;
}

export default React.memo(Maps);